      h1 3.4.	Estrategias de comunicación para responder preguntas e inquietudes

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p La gestión de preguntas e inquietudes en contextos de turismo étnico requiere del dominio de estrategias de comunicación intercultural técnicamente fundamentadas. La escucha activa se configura como una competencia básica, implicando atención plena, lenguaje corporal de apertura y paráfrasis para verificar la comprensión. 
      p Para el manejo de preguntas sensibles sobre rituales o creencias, se aplica el principio de discreción cultural, mediante respuestas validadas como: “Su pregunta aborda conocimientos profundos de nuestra comunidad que, por respeto a su significado sagrado, mantenemos en contextos ceremoniales específicos. Le puedo compartir, sin embargo, los principios de armonía con la naturaleza que guían nuestra relación con el territorio.” Esta aproximación se alinea con lo establecido en el Convenio 169 de la OIT sobre consulta y consentimiento previo, libre e informado.
      p Estas estrategias deben aplicarse de manera sistemática para garantizar diálogos respetuosos y enriquecedores entre los visitantes y las comunidades anfitrionas. A continuación se presenta el sistema integral de estrategias de cominucación:

